* {
    font-family: 'Sora', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.form {
    width: 500px ;
    margin: auto;
    padding: 50px;
    box-sizing: border-box;

    width: 100%;
    max-width: 700px;
    padding: 50px;
    background-color: #000;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);

    display: flex;
    flex-direction: column;
}

