.form {
    width: 500px ;
    margin: auto;
    padding: 50px;
    box-sizing: border-box;

    width: 100%;
    max-width: 700px;
    padding: 50px;
    background-color: #000;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);

    display: flex;
    flex-direction: column;
}

input[type=text] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
}

input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
}

#form button {
    color: #3A3A3C;
    background-color: #3A3A3C;
}
